body {
  padding: 0;
  margin: 0;
}

.container {
  margin-bottom: 150px;
}

h6 {
  line-height: 1.5 !important;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1rem;
}

.navbar-brand {
  letter-spacing: 2px;
  margin: 0 auto;
  font-family: "Lexend Peta", sans-serif;
  font-size: 2em;
  padding: 1rem;
}

.timer {
  font-size: 2.5rem;
  color: white;
}

.timer-row {
  margin-top: 2em;
}

.list-inline {
  margin-top: 2em;
  display: flex;
  justify-content: center;
}

#myTabContent {
  margin-top: 2em;
}

.nav-tabs {
  border-bottom: 0;
}

#fileHelp {
  margin-top: 10px;
}

#landing-gif {
  width: 100%;
  max-width: 600px;
}

.custom-file-label {
  color: #282828;
}

.ccc {
  margin: 8px;
  display: inline-flex;
}

.square-size-select {
  width: auto;
}

.ccontainer {
  position: relative;

  display: inline;
}
.ccontainer img {
  margin: 3px;
  display: block;
}
.ccontainer .fa-download {
  position: absolute;
  bottom: 0;
  left: 0;
}

input[type="file"] {
  color: transparent;
}
